.footer {
  background-color: var(--fill-color);
  padding-top: 10px;
  padding-bottom: 10px;
  border-top: 1px solid var(--border-line);
}

.container {
  width: 257px;
  height: 56px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .container {
    width: 100%;
    margin: 0 auto;
  }
}

.footer__text {
  color: var(--footer-text);

  margin-right: 4px;

  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;
  letter-spacing: 0.03em;
}

@media (min-width: 768px) {
  .footer__text {
    font-size: 12px;
    line-height: 1.25;
  }
}

.footer__svg {
  margin-right: 4px;
}

.footer__link {
  color: var(--footer-text);
  text-decoration: underline;
  letter-spacing: 0.03em;
  font-weight: 400;
  font-size: 10px;
  line-height: 1.2;
}

@media (min-width: 768px) {
  .footer__link {
    font-size: 12px;
    line-height: 1.25;
  }
}
